import React,{useState,useEffect} from 'react';
import axios from 'axios';

const SearchBar=()=>{
    const [term,setStateTerm]=useState('programming');
    const [debouncedTerm,setDeBouncedTerm]=useState(term);
    const [results,setResults]=useState([]);


    useEffect(()=>{
        const timeId=setTimeout(()=>{
            setDeBouncedTerm(term);
        },1000);

        return()=>{
            clearTimeout(timeId);
        }
    },[term]);

    useEffect(()=>{
        const search =async ()=>{
            const {data}= await axios.get('https://en.wikipedia.org/w/api.php',{
              params: {
                  action :'query',
                  list : 'search',
                  format :'json',
                  origin :'*',
                  srsearch : debouncedTerm
              }
          });
        
          setResults(data.query.search);
         };
       if(debouncedTerm){ 
           search();
       }

    },[debouncedTerm])
   
   
    const renderList=results.map((result)=>{

        return <div key={result.pageid} className='item'>
            <div className='right floated text'>
                <a href={`https://en.wikipedia.org?curid=${result.pageid}`}
                className='ui button'>Go</a>
            </div>
            <div className='content'>
                <div className='header'>
                    {result.title}
                </div>
                   <span dangerouslySetInnerHTML={{__html : result.snippet}}></span>
            </div>
        </div>
    });
    
    return <div>
        <div className='ui form'>
        <div className='field'>  
            <label>What You Want to Search!</label>
            <input value={term}
            onChange={e=>setStateTerm(e.target.value)}
             className='input'></input>
        </div>
       
    </div>
    <div className='ui celled list'>
        {renderList}
    </div>
    </div>
};

export default SearchBar;